import axios from 'axios'
import {
  Message,
  MessageBox
} from 'element-ui'

// export const url = "http://192.168.1.150"
// export const url = window.location.protocol+"//"+window.location.host
// export const url = 'http://pay-routine.vmajy.com'
// export const url = 'http://192.168.0.103:80'
export const url = window.serverUrl || window.location.protocol + '//' + window.location.host
// export const url = 'http://pay-adm.vmajy.com'
// export const url = 'http://117.50.41.223:46000'

const instance = axios.create({
  baseURL: url,
  timeout: 10000,
  headers: {
    'Content-type': 'application/x-www-form-urlencoded; charset=utf-8'
  }
})

// 
instance.interceptors.request.use((config) => {
  // headertoken
  if (sessionStorage.token) {
    config.headers['authorized'] = sessionStorage.token
  }
  // console.log(config,"config")
  if (config.jsonHeader) {
    config.headers['Content-type'] = 'application/json; charset=utf-8'
  } else {
    config.headers['Content-type'] = 'application/x-www-form-urlencoded; charset=utf-8'
  }
  return config
}, (error) => {
  // 
  return Promise.reject(error)
})

// 
instance.interceptors.response.use(response => {
  // 
  if (response.headers.accesstoken) {
    sessionStorage.token = response.headers.authorized
  }
  const res = response.data
  if (res.code !== 200) {
    if (res.code === 401 || res.code === 422) {
      MessageBox.alert('', '', {
        confirmButtonText: '',
        callback: action => {
          sessionStorage.clear()
          location.reload()
        }
      })
    } else if (res.code == 432) {
      Message({
        type: 'error',
        message: res.msg
      })
      setTimeout(() => {
        if (sessionStorage.token) {
          sessionStorage.clear()
          location.reload()
        }
      }, 1500)
    } else {
      Message({
        type: 'error',
        message: res.msg
      })
    }
    //  resolved  rejected
    return Promise.reject(new Error('error'))
  } else {
    return res
  }
}, error => {
  // 
  if (error.request) {
    Message({
      message: '',
      type: 'error'
    })
  } else {
    Message({
      message: error.message,
      type: 'error'
    })
  }
  return Promise.reject(error)
})

export default instance



