import request from '@/utils/request'
import qs from 'qs'

// 
export function login (username, password) {
  // Promise
  return request({
    url: '/merchant/admin/login',
    method: 'post',
    data: qs.stringify({
      username: username,
      password: password
    })
  })
}

// 
export function getSiteInfo() {
  return request({
    url: '/auth/admin/get_siteinfo',
    method: 'get',
    params: {}
  })
}






