<template>
    <div class="menu-wrapper">
        <el-submenu :index="item.path" v-if="item.visible === 1">
            <template slot="title">
                <svg-icon v-if="item.icon" :icon-class="item.icon" :class="item.icon" class="ico"></svg-icon>
                <span style="font-size: 15.5px">{{item.name}}</span>
            </template>
            <router-link  :to="resolvePath(i.path)"  v-for="i in item.children" :key="i.name" v-if="i.visible === 1">
                <el-menu-item :index="resolvePath(i.path)" :class="{styleObject: item.icon}">
                    <svg-icon v-if="i.icon" :icon-class="i.icon"></svg-icon>
                    <span slot="title">{{i.name}}</span>
                </el-menu-item>
            </router-link>
        </el-submenu>
    </div>
</template>

<script>
import path from 'path'

export default {
  name: 'slideBarItem',
  props: {
    item: {
      type: Object,
      required: true
    },
    baseUrl: {
      type: String,
      default: ''
    }
  },
  methods: {
    created(){
      console.log(this.item);
    },
    resolvePath (pa) {
      return path.resolve(this.baseUrl, pa)
    }
  }
}
</script>
<style>
.styleObject {
  padding-left: 56px!important;
}
</style>



