import { login } from '@/modules/login/api/login'
const user = {
  state: {
    isReload: true
  },
  mutations: {
    setReload: (state) => {
      state.isReload = false
    }
  },
  actions: {
    /*
     * 
     * */
    login ({commit}, userInfo) {
      return new Promise((resolve, reject) => {
        login(userInfo.username, userInfo.password).then(response => {
          sessionStorage.username = userInfo.username
          sessionStorage.token = response.obj.token
          sessionStorage.userType=response.obj.userType
          sessionStorage.name=response.obj.name
          sessionStorage.userId=response.obj.useId
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    /*
     * 
     * */
    logout ({commit}) {
      return new Promise((resolve) => {
        sessionStorage.clear()
        resolve()
      })
    },
    /*
    * Flag
    * */
    setReload ({commit}) {
      commit('setReload')
    }
  }
}

export default user



