import request from '@/utils/request'
import qs from 'qs'

// 
export function getUser (pageNumber, pageSize, queryList) {
  return request({
    url: '/merchant/user/list_rewrite',
    method: 'get',
    params: {
      pageNumber: pageNumber,
      pageSize: pageSize,
      name: queryList.name,
      phone: queryList.phone,
    }
  })
}

// 
export function addUser (userInfo) {
  return request({
    url: '/merchant/user/save_user',
    method: 'post',
    data: qs.stringify(
      userInfo
    )
  })
}

// 
export function editUser (userInfo) {
  return request({
    url: '/merchant/user/update_user',
    method: 'post',
    data: qs.stringify(
      userInfo
    )
  })
}

// 
export function resetPassword (id, password) {
  return request({
    url: '/merchant/user/reset_password',
    method: 'post',
    data: qs.stringify({
      id,
      password
    })
  })
}

// 
export function storeListByMctId (id) {
  return request({
    url: '/merchant/store/find_by_merchant_id',
    method: 'get',
    params: {
      id: id,
    }
  })
}

// 
export function userDetail (id) {
  return request({
    url: '/merchant/user/detail',
    method: 'get',
    params: {
      id: id,
    }
  })
}

// 
export function updatePwd (oldPassword, newPassword) {
  return request({
    url: '/merchant/user/modify_password',
    method: 'post',
    params: {
      password: oldPassword,
      newPassword: newPassword,
    }
  })
}

// 
export function getMealUrl () {
  return request({
    url: '/merchant/admin/app/weiqing_url',
    method: 'GET'
  })
}



