<template>
     <el-breadcrumb class="app-breadcrumb" separator="/">
      <template v-for="item in menuList">
        <el-breadcrumb-item v-if="item.name===''" :key="item.path" :to="item.path">
          <span style="color:#fff;"></span></el-breadcrumb-item>
        <el-breadcrumb-item v-else :key="item.path">
          <span style="color:#ebeef5;">{{item.name}}</span>
        </el-breadcrumb-item>
      </template>
      
    </el-breadcrumb>
</template>

<script>
export default {
  name: 'breadcrumb',
  created () {
    this.getBreadcrumb()
  },
  watch: {
    '$route' () {
      // 
      console.log(this.$route)
      this.getBreadcrumb()
    }
  },
  data () {
    return {
      menuList: null
    }
  },
  methods: {
    getBreadcrumb () {
      let matched = this.$route.matched.filter(item => item.name)
      console.log(matched)
      if (matched[0].name !== '') {
        matched = [{path: '/index', name: ''}].concat(matched)
      }
      console.log(matched)
      this.menuList = matched
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .app-breadcrumb.el-breadcrumb {
        display: inline-block;
        font-size: 14px;
        line-height: 60px;
        margin-left: 10px;
        .no-redirect {
            color: #97a8be;
            cursor: text;
        }
    }
    a{
      color: #ffffff !important;
    }
    .el-breadcrumb__inner{
      color: #ffffff !important;
    }
    .el-breadcrumb__item{
       color: #ffffff !important;
    }
</style>



