import request from '@/utils/request'
import qs from 'qs'

// LOGO
export function getMerchantDetail (merchantId) {
  return request({
    url: '/merchant/merchant/show_photo',
    method: 'get',
    params: {
    }
  })
}

//LOGO
export function editMerchant (photoId) {
  return request({
    url: '/merchant/merchant/update_photo ',
    method: 'post',
    data: qs.stringify({
      photoId: photoId
    })
  })
}



