<template>
  <el-menu class="navbar" mode="horizontal">
    <div class="hamburger-container">
      <a href="#/index/index" style="position: relative; display: inline-block; width: 100px; height: 40px;">
        <img v-if="siteInfo.logo" :src="siteInfo.logo" alt="logo" style=" max-width:100%;max-height:100%">
      </a>
    </div>
    <hamburger class="hamburger-container" :toggleClick="toggleBar" :isActive="isClosed"></hamburger>
    <breadcrumb></breadcrumb>
    <div class="avatar-container" style="right: 250px; bottom: 1px;">
      <el-button size="mini">
        <el-link type="primary" :underline="false" @click="turnToAddMeal"></el-link>
      </el-button>
    </div>
    <div class="avatar-container username hover-container" style="right: 360px !important; color: #fff; cursor: pointer;">
      <i class="el-icon-user-solid high-bright"></i> 
      <span class="high-bright">&nbsp;{{username}}</span>
    </div>
    <div class="avatar-container messageicon">
      <!-- <img src="../../../assets/images/nav/mes.png" alt="" width="16px"> -->
    </div>
    <el-dropdown class="avatar-container" trigger="click">
      <span class="el-dropdown-link hover-container">
        <span class="high-bright"> </span><i class="el-icon-arrow-down el-icon--right high-bright"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item @click.native="showUserDataDialogOpen"></el-dropdown-item>
        <el-dropdown-item @click.native="changeUserInfoDialogOpen"></el-dropdown-item>
        <el-dropdown-item @click.native="resetPasswordDialogOpen"></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <div class="avatar-container powerIcon hover-container" @click="logout">
      <i class="el-icon-switch-button high-bright">&nbsp; <span class="high-bright"></span></i>
    </div>
    <el-dialog
      title=""
      :visible.sync="resetPasswordDialog"
      width="30%"
      :before-close="resetPasswordDialogClose" append-to-body>
      <el-form :model="resetForm" ref="resetForm" label-width="100px" class="12345">
        <el-form-item label="" prop="password">
          <el-input v-model="resetForm.oldPassword" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="resetForm.newPassword" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="resetForm.confirmPassword" type="password"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="resetPasswordDialogClose"> </el-button>
        <el-button type="primary" @click="saveNewPssword"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="showUserDataDialog"
      width="30%"
      :before-close="showUserDataDialogClose"  append-to-body>

      <div class="box-card mr20" style="width:90%">

        <div class="box-content">

            <div class="info-li" line-height="50px">
              <span class="info-label">    </span>
              <span class="info-value">{{baseInfo.name || '-'}}</span>
            </div>
            <div class="info-li">
              <span class="info-label">  </span>
              <span class="info-value">{{baseInfo.username || '-'}}</span>
            </div>
            <div class="info-li">
              <span class="info-label">  </span>
              <span class="info-value">{{baseInfo.phone || '-'}}</span>
            </div>
            <div class="info-li">
              <span class="info-label"></span>
              <span class="info-value">{{baseInfo.storeName || '-'}}</span>
            </div>            
            <div class="info-li">
              <span class="info-label"></span>
              <span class="info-value">{{baseInfo.createTime || '-'}}</span>
            </div>

        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showUserDataDialogClose"> </el-button>
      </span>
    </el-dialog>

    <!---->
    <el-dialog
      title=""
      :visible.sync="changeUserInfoDialog"
      width="30%"
      :before-close="changeUserInfoDialogClose"  append-to-body>

      <div>
        <el-form ref="infoForm" :model="infoForm" label-width="120px">
          <el-form-item label="LOGO">
            <el-upload
              class="logo-uploader"
              :action="url"
              :data="filesData"
              name="files"
              :show-file-list="false"
              :on-success="successSubmitImage"
              :on-error="failedSubmitImage"
              :before-upload="checkImage"
              :headers="headers">
              <img v-if="faceImgURL" :src="faceImgURL" class="logo"/>
              <i v-else class="el-icon-plus logo-uploader-icon"></i>
            </el-upload>
          </el-form-item>
        </el-form>

      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="changeUserInfoDialogClose"> </el-button>
        <el-button type="primary" @click="saveInfo"> </el-button>
      </span>
    </el-dialog>

  </el-menu>
</template>
<script>
import {mapGetters} from 'vuex'
import breadcrumb from '@/components/breadcrumb'
import hamburger from '@/components/hamburger'
import {MessageBox} from 'element-ui'
import {updatePwd, getMealUrl} from '@/modules/user/api/user'
import {getMerchantDetail, editMerchant} from '@/modules/layout/components/api/merchant'
import {returnThumbnailUrl, uploadUrl} from '@/modules/file/api/upload' // API
import { userDetail } from '@/modules/user/api/user'
export default {
  name: 'navbar',
  data () {
    return {
      username: '' + (sessionStorage.name ? sessionStorage.name : sessionStorage.username),
      userType: '', // 
      resetPasswordDialog: false, // 
      showUserDataDialog: false, // 
      changeUserInfoDialog: false, // 
      resetForm: {
        oldPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      infoForm: {
        faceImgId: '' // id
      }, // id
      faceImgURL: '', // 
      //* **************************file upload
      filesData: {
        module: 'image'
      },
      url: uploadUrl + '/user',
      headers: {
        authorized: sessionStorage.token
      },
      mealUrlVal: '',
      baseInfo: {}, // 
      siteInfo: {
          logo: ''
      }
    }
  },
  components: {
    breadcrumb,
    hamburger
  },
  mounted () {
    /** 2019.05.31 xu**/
    this.userType = sessionStorage.userType || '3'
    this.mountedGetPhoto()
    this.getMealUrlCall()
    //LOGO
    if (sessionStorage.getItem('logoImg')) {
      this.siteInfo.logo = sessionStorage.getItem('logoImg')
    }
  },
  methods: {
    /**
     * 
     **/
    turnToAddMeal () {
      if (this.mealUrlVal) {
        window.open(this.mealUrlVal)
      } else {
        this.getMealUrlCall(1)
      }
    },
    /**
     * 
     * */
    getMealUrlCall (val) {
      getMealUrl().then(res => {
        if (val) {
          this.mealUrlVal = res.obj
          window.open(res.obj)
        } else {
          this.mealUrlVal = res.obj
        }
      })
    },
    toggleBar () {
      this.$store.dispatch('toggleBar')
    },
    logout () {
      MessageBox.confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'info'
      }).then(() => {
        sessionStorage.clear()
        location.reload()
      })
    },
    // 
    resetPasswordDialogOpen: function () {
      console.log(111)
      // ID
      this.resetPasswordDialog = true
    },
    resetPasswordDialogClose: function () {
      this.resetForm.password = ''
      this.resetPasswordDialog = false
    },
    saveNewPssword: function () {
      if (this.resetForm.newPassword != this.resetForm.confirmPassword) {
        this.$message({
          message: '',
          type: 'error'
        })
        return
      }
      // 
      updatePwd(this.resetForm.oldPassword, this.resetForm.newPassword).then(response => {
        this.$message({
          message: response.msg,
          type: 'success'
        })
        this.resetForm.password = ''
        this.resetPasswordDialog = false
      }).catch(() => {
        this.loading = false
      })
    },
    /**
       *   2019-11-22
       */
    showUserDataDialogOpen: function () {
      this.getBaseInfo()

      this.showUserDataDialog = true
    },
    showUserDataDialogClose: function () {
      
      this.showUserDataDialog = false
    },
    /**
       *   2019-05-31
       */
    changeUserInfoDialogOpen: function () {
      this.mountedGetPhoto()

      this.changeUserInfoDialog = true
    },
    changeUserInfoDialogClose: function () {
      this.infoForm.faceImgId = ''
      this.changeUserInfoDialog = false
    },
    // 
    getBaseInfo () {
      userDetail(sessionStorage.userId).then(res => {
        this.baseInfo = res.obj
      })
    },       
    /**
       * 
       */
    mountedGetPhoto: function () {
      getMerchantDetail().then(res => {
        let data = res.msg
        if (data === '') {
          let base64 = 'data:image/png;base64,'
          this.faceImgURL = base64
        } else {
          this.infoForm.faceImgId = data
          sessionStorage.setItem('face', data)
          this.faceImgURL = returnThumbnailUrl(data)
        }
      }).catch(e => {

      })
    },

    /**
       * 
       */
    getImg: function (val) {
      let reg = /^[0-9]*$/
      if (reg.test(val)) {
        return returnThumbnailUrl(val)
      } else {
        let base64 = 'data:image/png;base64,'
        return base64
      }
    },
    saveInfo: function () {
      let pid = this.infoForm.faceImgId
      if (pid == '') {
        this.$message.error('')
        return
      }
      editMerchant(pid).then(res => {
        this.$message.success(res.msg)
        this.changeUserInfoDialogClose()
      }).catch(e => {

      })
    },
    successSubmitImage (response, file) {
      // ID
      console.log(response)
      this.$message({
        message: '',
        type: 'success'
      })
      this.infoForm.faceImgId = response.obj
      this.faceImgURL = returnThumbnailUrl(response.obj)
    },

    /**
       * 
       */
    failedSubmitImage () {
      this.$message({
        message: '',
        type: 'error'
      })
    },
    /**
       * 
       */
    checkImage (file) {
      let isImage = file.type.substring(0, 5) === 'image'
      if (!isImage) {
        this.$message.error('jpg / jpeg / png')
        return false
      }
      return true
    }

  },
  computed: {
    ...mapGetters([
      'isClosed'
    ])
  }
}
</script>

<style lang="scss">
  .avatar-container{
    i,.el-dropdown-link{
      color:#fff;
    }
  }

</style>


