import router from './router'
import store from './store'

import { getMenu } from '@/modules/system/api/menu'
import NProgress from 'nprogress' // Progress 
import 'nprogress/nprogress.css'// Progress 

import { registerRoutes } from './utils/getViews'

// 
router.beforeEach((to, from, next) => {
  NProgress.start()
  if (to.path !== '/login') {
    // token
    if (!sessionStorage.token && to.path !== '/fogot') {
      next({path: '/login'})
      NProgress.done()
    } else if (to.path === '/fogot') {
      /**
       * 20190506  xwy
       */
      next()
    } else {
      // routesvuex
      if (store.getters.isReload) {
        store.dispatch('setReload')
        let routes
        getMenu().then(response => {
          routes = registerRoutes(response.obj)
          routes.push({
            path: '*',
            redirect: '/404'
          })
          router.addRoutes(routes)
          next({...to, replace: true}) // addroutes
        }).catch(() => {
        })
      } else {
        next()
      }
    }
  } else {
    sessionStorage.clear()
    next()
  }
})
router.afterEach(() => {
  NProgress.done()
})



