<template>
  <div class="shadow">
    <!-- <div class="logo" v-if="!isClosed">
      <img v-if="siteInfo.logo" :src="siteInfo.logo" alt="">
    </div> -->

    <el-menu
      class="el-menu-vertical"
      :default-active="$route.path"
      :collapse="isClosed"
      background-color="black"
      text-color="#fff"
      style="overflow: auto">
      <slide-bar-item v-for="item in menuList" :key="item.name" :item="item" :base-url="item.path"></slide-bar-item>
    </el-menu>
  </div>
</template>

<script>
  import slideBarItem from './slideBarItem'
  import {getMenu} from '@/modules/system/api/menu'

  import {mapGetters} from 'vuex'
import extend from 'extend';

  export default {
    components: {slideBarItem},
    name: 'index',
    data() {
      return {
        menuList: [],
        siteInfo: {
          logo: ''
        },
      }
    },
    created() {
      this.getMenuList()
    },
    mounted() {
      //LOGO
      if (sessionStorage.getItem('logoImg')) {
        this.siteInfo.logo = sessionStorage.getItem('logoImg')
      }
    },
    methods: {
        getMenuList() {
        let userType = sessionStorage.getItem('userType')
        getMenu().then(response => {
          let data = response.obj
          let list = []
          for(let i = 0; i < data.length; i++){
            data[i].children.forEach(element => {
            console.log(element.id==='1193789116351746048',"children")
            if(element.id==='1193789116351746048'){
              console.log(111)
              // continue
            }
            // if(element.id == "1193789116351746048"){
            //   continue 
            // }
            });
            
           
            list.push(data[i])
          }
          this.menuList = list
          console.log(this.menuList)
        }).catch(() => {
        })
      }
    },
    computed: {
      ...mapGetters([
        'isClosed'
      ])
    }
  }
</script>

<style lang="scss">
  .menu-title{
    height: 3rem;
    width: 100%;
    color: #fff;
    font-size: 1.3rem;
    line-height: 3rem; 
    padding-left: 1.8rem;
  }
</style>


